//
 function getNodeIn(nodes, connections) {
    return nodes.map(node => {
        node.in = 0;
        for (let index = 0; index < connections.length; index++) {
            if (connections[index].destination.id === node.id) {
                node.in++;
            }
        }
        return node
    })
}



/**
 * 
 * 
 * @param nodes  
 * @param connections 
 * 
 *  nodes
 *   1 
 *   2 0nodesconnections
 *
 */
export  function interpretationLayer(nodes, connections) {
    console.log(nodes, connections)
    let resQueue = []//
    //
    while (nodes.length) {
        //
        nodes = getNodeIn(nodes, connections)
        /**
         *  
         *     
         */
            // let tempNodes = Object.assign({}, nodes)
        let tempNodes = nodes
        for (let i = 0; i < tempNodes.length; i++) {
            if (tempNodes[i].in === 0) {
                //0
                resQueue.push(tempNodes[i])
                // nodes
                nodes = nodes.filter(item => {
                    return item !== tempNodes[i]
                })
                // connections
                connections = connections.filter(connection => {
                    return connection.source.id !== tempNodes[i].id
                })
            }
        }
    }
    let res = {
        connections: connections,
        nodes: resQueue
    }

    return res
}
