<template>
  <el-card class="upload-container">
    <el-upload
        :data="dataObj"
        multiple
        :limit="limit"
        :list-type="listType"
        :show-file-list="showFileList"
        :before-upload="beforeAvatarUpload"
        class="image-uploader image-upload-border"
        accept=".csv"
        :on-remove="rmImage"
        :on-exceed="outLimit"
        :file-list="fileList"
        :auto-upload="true"
        :http-request="uploadSectionFile"
        action="123"
    >
      <i class="el-icon-upload"/>
      <div class="el-upload__text">csv<em></em>csv</div>
    </el-upload>
  </el-card>
</template>

<script>
import {uploadpic} from '@/api/singleImage'
// import { getToken } from "@/utils/auth";

export default {
  name: 'SingleImageUpload',
  props: {
    limit: {type: Number, default: 1}, //
    listType: {type: String, default: 'picture'}, // text/picture/picture-card
    pathname: {type: String, default: 'file'},
    showFileList: {type: Boolean, default: true} //
  },
  data() {
    return {
      fileMsgAndUrl: [], //UIDurl
      fileUrlList: [], // url
      fileList: [], //
      tempUrl: '',
      dataObj: {Authorization: ''}
    };
  },
  mounted() {

  },
  methods: {
    // 
    beforeAvatarUpload(file) {
      // const isLt10M = file.size / 1024 / 1024 < 10;
      // if (!isLt10M) {
      //   this.$message.error(' 10MB!');
      // }
      // return isLt10M;
    },
    //
    uploadSectionFile(param) {
      this.$message({
        message: '',
        type: 'info'
      });
      var fileObj = param.file;
      // FormData 
      var form = new FormData();
      // 
      form.append(this.pathname, fileObj);
      let _this = this;
      uploadpic(form).then(res => {
        // console.log(res);
        if (res.msg === "") {
          let url = {
            uid: fileObj.uid,
            url: res.data
          };
          // console.log(res.data.list[0]);
          _this.fileMsgAndUrl.push(url);
          _this.fileUrlList.push(res.data);
          this.$message({
            message: '',
            type: 'success'
          });
          // 
          _this.$emit('uploadSuccess', _this.fileUrlList);

        } else {
          // 
          _this.$emit('error', res.msg);
        }
      });
    },
    init() {
      // this.dataObj.Authorization = getToken();
      this.rmImages();
    },
    //
    outLimit() {
      // this.$emit("outLimit","" );
      this.$message({
        message: '',
        type: 'warning'
      });
    },
    //  val
    rmImage(val) {
      this.fileMsgAndUrl = this.fileMsgAndUrl.filter(
          item => item.uid !== val.uid
      );
      this.fileUrlList = this.fileMsgAndUrl.map(item => item.url);
      this.$emit('uploadSuccess', this.fileUrlList);
    },
    //
    rmImages() {
      this.fileUrlList = [];
      this.fileList = [];
      this.fileUrlList = [];
      // this.$emit('uploadSuccess', []);
    }

  }
};
</script>


<style scoped lang="less">
.upload-container {
  .image-upload-border {
    .el-upload__text{
      font-size: 1rem;
    }
  }
}
</style>
